.modal {
    width: 30%;
    min-width: 300px;
    padding: 2rem 3rem;
    background-color: #f5f5f5;
    color: #222;
    border-radius: 5px;
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.5);
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s ease;
    z-index: 100;
    position: absolute;
    transform: translateY(-100%);
}

.modal.visible {
    opacity: 1;
    pointer-events: all;
    transform: translateY(0);
}

.heading {
    color: var(--prim-color);
    text-transform: uppercase;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 2rem;
    border-bottom: 1px solid #cccccccc;
    padding-bottom: 1rem;
}

.form {
    margin-top: 1.5rem;
}

.formControl {
    display: flex;
    flex-flow: column;
    margin-bottom: 1.5rem;
}

.formControl:last-of-type {
    margin-bottom: 0;
}

.formControl label {
    text-transform: uppercase;
    color: var(--prim-color);
    font-weight: bold;
    margin-bottom: 0.5rem;
    text-align: left;
}

.formControl label.empty {
    color: var(--error-color);
}

.formControl input {
    padding: 0.65rem 0.75rem;
    border-radius: 5px;
    border: 2px solid var(--prim-color);
    font-size: 1rem;
    font-weight: 600;
    color: #555;
}

.formControl input::placeholder {
    color: #cecece;
}

.formControl input.emptyInput {
    border: 2px solid var(--error-color);
    background-color: #fff4f4;
}

.blankMsg {
    text-align: left;
    margin-top: 0.25rem;
    color: var(--error-color);
    visibility: hidden;
}

.blankMsg.show {
    visibility: visible;
}

.btns {
    display: flex;
    margin: 3rem 0 1rem 0;
    justify-content: flex-end;
}

.btns Button {
    width: 7rem;
    margin-right: 2rem;
    margin-top: 0;
}

.btns Button:last-of-type {
    margin-right: 0;
}