:root {
    --prim-color: #5ab5df;
    --prim-color-hover: #489fc7;
    --bg-color: #242333;
    --bg-color-hover: #212030;
    --error-color: #e43f3f;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: linear-gradient(100deg,#252435 50%, #242333 0);
    height: 100vh;
    color: #f4f4f4;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.container {
    width: 40%;
    height: 100vh;
    margin: 0 auto;
    min-width: 350px;
    display: flex;
    justify-content: center;
    align-items: center;
}
